@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Mono:wght@500;700&display=swap);
.container {
  height: 100%;
  max-width: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  padding: 1rem;
}

.slider {
  margin-top: 2rem;
}
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.gameTab {
  max-width: 425px;
  padding: 16px;
}

.gameTable {
  font-family: 'Roboto Mono', monospace;
  width: 100%;
}

.guessLetter {
  background-color: #FFE082;
}
